import { NextRequest, NextResponse } from 'next/server'
import { supabaseAdmin } from '@/lib/supabase/server'
import { v4 as uuidv4 } from 'uuid'
import type { Integration, IntegrationType } from '@/lib/accelerator/types'

// ==========================================
// GET /api/v1/integrations - List integrations
// ==========================================
export async function GET(request: NextRequest) {
  try {
    const supabase = supabaseAdmin
    const { searchParams } = new URL(request.url)
    
    const subAccountId = searchParams.get('subAccountId')
    const integrationId = searchParams.get('id')
    const integrationType = searchParams.get('type') as IntegrationType | null
    const activeOnly = searchParams.get('activeOnly') !== 'false'
    const page = parseInt(searchParams.get('page') || '1')
    const limit = Math.min(parseInt(searchParams.get('limit') || '50'), 100)

    if (!subAccountId) {
      return NextResponse.json(
        { success: false, error: 'subAccountId is required' },
        { status: 400 }
      )
    }

    // If specific integration requested
    if (integrationId) {
      const { data: integration, error } = await supabase
        .from('integrations')
        .select('*')
        .eq('id', integrationId)
        .eq('sub_account_id', subAccountId)
        .single()

      if (error || !integration) {
        return NextResponse.json(
          { success: false, error: 'Integration not found' },
          { status: 404 }
        )
      }

      return NextResponse.json({
        success: true,
        data: transformIntegration(integration)
      })
    }

    // List all integrations
    let query = supabase
      .from('integrations')
      .select('*', { count: 'exact' })
      .eq('sub_account_id', subAccountId)
      .order('created_at', { ascending: false })

    if (integrationType) {
      query = query.eq('integration_type', integrationType)
    }

    if (activeOnly) {
      query = query.eq('is_active', true)
    }

    // Pagination
    const from = (page - 1) * limit
    const to = from + limit - 1
    query = query.range(from, to)

    const { data: integrations, error, count } = await query

    if (error) {
      console.error('Error fetching integrations:', error)
      return NextResponse.json(
        { success: false, error: 'Failed to fetch integrations' },
        { status: 500 }
      )
    }

    const transformedIntegrations = (integrations || []).map(transformIntegration)

    return NextResponse.json({
      success: true,
      data: transformedIntegrations,
      pagination: {
        page,
        limit,
        total: count || 0,
        totalPages: Math.ceil((count || 0) / limit)
      }
    })

  } catch (error) {
    console.error('Error in GET /api/v1/integrations:', error)
    return NextResponse.json(
      { success: false, error: 'Internal server error' },
      { status: 500 }
    )
  }
}

// ==========================================
// POST /api/v1/integrations - Create integration
// ==========================================
export async function POST(request: NextRequest) {
  try {
    const supabase = supabaseAdmin
    const body = await request.json()

    const {
      subAccountId,
      type,
      name,
      apiKey,
      apiSecret,
      accessToken,
      refreshToken,
      tokenExpiresAt,
      config = {}
    } = body

    if (!subAccountId || !type || !name) {
      return NextResponse.json(
        { success: false, error: 'subAccountId, type, and name are required' },
        { status: 400 }
      )
    }

    // Check if integration of this type already exists
    const { data: existing } = await supabase
      .from('integrations')
      .select('id')
      .eq('sub_account_id', subAccountId)
      .eq('integration_type', type)
      .single()

    if (existing) {
      return NextResponse.json(
        { success: false, error: 'Integration of this type already exists. Please update the existing one.' },
        { status: 409 }
      )
    }

    const integrationId = uuidv4()
    const { data: integration, error } = await supabase
      .from('integrations')
      .insert({
        id: integrationId,
        sub_account_id: subAccountId,
        integration_type: type,
        name,
        api_key: apiKey,
        api_secret: apiSecret,
        access_token: accessToken,
        refresh_token: refreshToken,
        token_expires_at: tokenExpiresAt,
        config,
        is_active: true,
        sync_status: 'never_synced'
      })
      .select()
      .single()

    if (error) {
      console.error('Error creating integration:', error)
      return NextResponse.json(
        { success: false, error: 'Failed to create integration' },
        { status: 500 }
      )
    }

    return NextResponse.json({
      success: true,
      data: transformIntegration(integration)
    }, { status: 201 })

  } catch (error) {
    console.error('Error in POST /api/v1/integrations:', error)
    return NextResponse.json(
      { success: false, error: 'Internal server error' },
      { status: 500 }
    )
  }
}

// ==========================================
// PUT /api/v1/integrations - Update integration
// ==========================================
export async function PUT(request: NextRequest) {
  try {
    const supabase = supabaseAdmin
    const body = await request.json()

    const {
      id,
      subAccountId,
      name,
      apiKey,
      apiSecret,
      accessToken,
      refreshToken,
      tokenExpiresAt,
      config,
      isActive
    } = body

    if (!id || !subAccountId) {
      return NextResponse.json(
        { success: false, error: 'id and subAccountId are required' },
        { status: 400 }
      )
    }

    // Build update object
    const updates: Record<string, any> = {
      updated_at: new Date().toISOString()
    }

    if (name !== undefined) updates.name = name
    if (apiKey !== undefined) updates.api_key = apiKey
    if (apiSecret !== undefined) updates.api_secret = apiSecret
    if (accessToken !== undefined) updates.access_token = accessToken
    if (refreshToken !== undefined) updates.refresh_token = refreshToken
    if (tokenExpiresAt !== undefined) updates.token_expires_at = tokenExpiresAt
    if (config !== undefined) updates.config = config
    if (isActive !== undefined) updates.is_active = isActive

    const { data: integration, error } = await supabase
      .from('integrations')
      .update(updates)
      .eq('id', id)
      .eq('sub_account_id', subAccountId)
      .select()
      .single()

    if (error || !integration) {
      console.error('Error updating integration:', error)
      return NextResponse.json(
        { success: false, error: 'Failed to update integration' },
        { status: 500 }
      )
    }

    return NextResponse.json({
      success: true,
      data: transformIntegration(integration)
    })

  } catch (error) {
    console.error('Error in PUT /api/v1/integrations:', error)
    return NextResponse.json(
      { success: false, error: 'Internal server error' },
      { status: 500 }
    )
  }
}

// ==========================================
// DELETE /api/v1/integrations - Delete integration
// ==========================================
export async function DELETE(request: NextRequest) {
  try {
    const supabase = supabaseAdmin
    const { searchParams } = new URL(request.url)
    
    const id = searchParams.get('id')
    const subAccountId = searchParams.get('subAccountId')

    if (!id || !subAccountId) {
      return NextResponse.json(
        { success: false, error: 'id and subAccountId are required' },
        { status: 400 }
      )
    }

    const { error } = await supabase
      .from('integrations')
      .delete()
      .eq('id', id)
      .eq('sub_account_id', subAccountId)

    if (error) {
      console.error('Error deleting integration:', error)
      return NextResponse.json(
        { success: false, error: 'Failed to delete integration' },
        { status: 500 }
      )
    }

    return NextResponse.json({ success: true }, { status: 200 })

  } catch (error) {
    console.error('Error in DELETE /api/v1/integrations:', error)
    return NextResponse.json(
      { success: false, error: 'Internal server error' },
      { status: 500 }
    )
  }
}

// ==========================================
// HELPER FUNCTIONS
// ==========================================

function transformIntegration(dbIntegration: any): Omit<Integration, 'accessToken' | 'refreshToken' | 'apiKey' | 'apiSecret'> & {
  hasCredentials: boolean
} {
  return {
    id: dbIntegration.id,
    subAccountId: dbIntegration.sub_account_id,
    type: dbIntegration.integration_type,
    name: dbIntegration.name,
    config: dbIntegration.config || {},
    isActive: dbIntegration.is_active,
    lastSyncAt: dbIntegration.last_sync_at,
    syncStatus: dbIntegration.sync_status,
    syncError: dbIntegration.sync_error,
    tokenExpiresAt: dbIntegration.token_expires_at,
    createdAt: dbIntegration.created_at,
    updatedAt: dbIntegration.updated_at,
    // Don't expose actual credentials, just indicate if they exist
    hasCredentials: !!(dbIntegration.api_key || dbIntegration.access_token)
  }
}

